@use "../work/mixins";
@use "../work/fonts";
@use "../work/colors";
.portfolio {
  &__title {
    margin-bottom: 4.7rem;
  }

  &__item {
    margin-bottom: 10rem;
    box-shadow: 0 0.3rem 1rem rgba(192, 192, 192, 0.1);

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    position: relative;
    display: block;
    margin-bottom: 2.5rem;
    max-width: 100%;
    max-height: 52rem;
  }
  &__link.dev:after {
    position: absolute;
    content: "in work";
    top: 0;
    left: 0;
    font-size: 2rem;
    font-weight: 500;
    padding: 1rem 3rem;
    color: rgb(241, 241, 241);
    background: #ea5241;

    text-shadow: -1px -1px 1px rgba(255, 255, 255, 0.1),
      1px 1px 1px rgba(0, 0, 0, 0.5);

    box-shadow: 0px 10px 13px -7px #000000, 5px 5px 15px 5px rgba(0, 0, 0, 0);
  }

  &__text--link {
    display: inline-block;
    margin-bottom: .5rem;
    // text-decoration: underline;

    transition: color 0.3s;

    &:hover {
      color: colors.$color1;
    }
  }

  &__description {
    padding-bottom: 1rem;
    font-size: 1.5rem;
    & a {
      transition: color 0.3s;

      &:hover {
        color: colors.$color1;
      }
    }
  }
}
